import React, { useState, useEffect, useCallback, useLayoutEffect, useMemo, createRef, useRef } from 'react';
import Nav from './Nav';
import '../style/App.scss';
import Body from './Body';
import gsap from 'gsap';
import BODIES from './bodies.js';
import { debounce, throttle, scrollTo } from './utils.js';

const App = function () {

	// Current section
	let [current, setCurrent] = useState(BODIES[0].id);
	// Is transition ended ?
	let [transitionEnd, setTransitionEnd] = useState(false);
	// Translate value on touch
	let [translate, setTranslate] = useState(0);
	let [origin, setOrigin] = useState(null);
	// Sections references
	let refs = useMemo(() => ({
		'soleil': createRef(null),
		'mercure': createRef(null),
		'venus': createRef(null),
		'terre': createRef(null),
		'mars': createRef(null),
		'jupiter': createRef(null),
		'saturne': createRef(null),
		'uranus': createRef(null),
		'neptune': createRef(null),
		'pluton': createRef(null)
	}), []);
	let appContainer = useRef(null);

	let [swipe, setSwipe] = useState(true);

	// On component load
	useEffect(() => {

		const hasCompass = window.DeviceOrientationEvent && 'ontouchstart' in window;
		const handleRotate = throttle(event => {
			let gamma = (event.gamma || 0) / 2;
			let beta = (event.beta || 0) / 2;

			Object.values(refs).forEach(el => {
				el.current.getElementsByTagName('img')[0].style.transform = 'translate3d(' + gamma + 'px, ' + beta + 'px, 0px)';
			});
		}, 16);

		if (hasCompass) {
			window.addEventListener('deviceorientation', handleRotate, false);
		} else {
			gsap.to(".system-body-left", { duration: 10, y: 15, x: -7, repeat: -1, yoyo: true, ease: "power1.inOut" });
			gsap.to(".system-body-right", { duration: 10, y: 15, x: 7, repeat: -1, yoyo: true, ease: "power1.inOut" });
		}

		const currentSection = refs.soleil.current;
		appContainer.current.style.height = currentSection.offsetHeight + 'px';

		const startTl = gsap.timeline({ defaults: { duration: .2 } });
		startTl.to(currentSection.getElementsByTagName('h1'), { opacity: 1 });
		startTl.to(currentSection.getElementsByTagName('ul'), { opacity: 1 });
		startTl.fromTo(currentSection.getElementsByTagName('li'), { x: -10 }, { opacity: 1, x: 0, stagger: 0.1 }, '-=.2');
		startTl.fromTo(currentSection.querySelectorAll('.description-content'), { y: -10 }, { opacity: 1, y: 0, stagger: 0.1 });
		startTl.then(() => {
			setTransitionEnd(true);
		});

		return () => {
			if (hasCompass) {
				window.removeEventListener('deviceorientation', handleRotate);
			}
		}
	}, [refs, setTransitionEnd]);

	const swipeTl = useMemo(() => gsap.timeline({ defaults: { duration: .5 }, delay: 1, repeat: -1 }), []);
	const swipeEl = useMemo(() => document.createElement('div'), []);

	useEffect(() => {

		if (swipe) {

			swipeEl.classList.add('swipe');

			const swipeTouch = document.createElement('div');
			swipeTouch.classList.add('touch');
			swipeEl.append(swipeTouch);

			const swipeTouchEvent = document.createElement('div');
			swipeTouchEvent.classList.add('touch-event');
			swipeEl.append(swipeTouchEvent);

			const currentSection = refs.soleil.current;
			currentSection.append(swipeEl);

			swipeTl.to(swipeTouchEvent, { opacity: 1, duration: .2 });
			swipeTl.to(swipeTouchEvent, { height: '31px', width: '31px', duration: .2 }, '-=0.2');
			swipeTl.to(swipeTouchEvent, { opacity: 0, duration: .4 });
			swipeTl.to(swipeTouch, { opacity: 1 }, '-=0.6');
			swipeTl.to(swipeEl, { left: '32px' }, '-=0.2');
			swipeTl.to(swipeTouch, { opacity: 0, duration: .6 });

		} else {
			swipeTl.repeat(1);
			swipeTl.then(() => {
				console.log('remove');
				swipeEl.remove();
			});
		}
	}, [appContainer, refs, setSwipe, swipe, swipeEl, swipeTl]);

	// Goto section
	const goto = useCallback(next => {
		setTransitionEnd(false);
		if (swipe) {
			setSwipe(false);
		}

		const currentSection = refs[current].current;
		const nextSection = refs[next].current;
		const leftPosition = nextSection.offsetLeft;

		let direction = 'right'; // scroll to the right
		if (currentSection.offsetLeft > leftPosition) {
			direction = 'left' // scroll to the left
		}

		const distance = 2;//(Math.abs((currentSection.offsetLeft - leftPosition) / window.innerWidth) / 2) + 1;

		scrollTo(0).then(() => {
			const scrollTl = gsap.timeline({ defaults: { duration: .2 } });

			// Before scroll
			scrollTl.fromTo(currentSection.querySelectorAll('.description-content'), { opacity: 1, y: 0 }, { opacity: 0, y: -10, stagger: -0.1 });
			scrollTl.fromTo(currentSection.getElementsByTagName('li'), { opacity: 1, x: 0 }, { opacity: 0, x: -10, stagger: -0.1 });
			scrollTl.fromTo(currentSection.getElementsByTagName('ul'), { opacity: 1 }, { opacity: 0, duration: 0.4 }, '-=.1');
			scrollTl.to(currentSection.getElementsByTagName('h1'), { opacity: 0, x: direction === 'left' ? '-350%' : '350%', ease: "power4.inOut", duration: distance }, '-=.4');

			// Scroll
			scrollTl.to(".container", { duration: distance, x: leftPosition * -1, ease: "power4.inOut" }, '-=' + distance);

			// After scroll
			scrollTl.fromTo(nextSection.getElementsByTagName('h1'), { opacity: 0, x: direction === 'left' ? '350%' : '-350%' }, { opacity: 1, x: 0, ease: "power4.inOut", duration: distance }, '-=' + distance);
			scrollTl.fromTo(nextSection.getElementsByTagName('ul'), { opacity: 0 }, { opacity: 1 }, '-=.4');
			scrollTl.fromTo(nextSection.getElementsByTagName('li'), { opacity: 0, x: -10 }, { opacity: 1, x: 0, stagger: 0.1 }, '-=.2');
			scrollTl.fromTo(nextSection.querySelectorAll('.description-content'), { opacity: 0, y: -10 }, { opacity: 1, y: 0, stagger: 0.1 });
			scrollTl.then(() => {
				setTransitionEnd(true);
				appContainer.current.style.height = nextSection.offsetHeight + 'px';
			});

			setCurrent(next);
		});

		// window.scrollTo({ top: 0, behavior: 'smooth' }).then(() => {
		// });
	}, [appContainer, current, refs, setCurrent, setTransitionEnd, setSwipe, swipe]);

	// Handle mouse wheel to change section
	const handleMouseWheel = useCallback(debounce(e => {
		if (transitionEnd) {
			const currentSection = refs[current].current;
			let nextSection = null;
			if (e.deltaY === 0) {
				if (e.deltaX < 0) {
					nextSection = currentSection.previousElementSibling;
				} else {
					nextSection = currentSection.nextElementSibling;
				}
				if (nextSection != null) {
					goto(nextSection.getAttribute('id'));
				}
			}
		}
	}, 500, true), [current, goto, refs, transitionEnd]);

	useLayoutEffect(() => {
		const container = appContainer.current;
		container.addEventListener('wheel', handleMouseWheel);
		return () => container.removeEventListener('wheel', handleMouseWheel);
	}, [handleMouseWheel, appContainer]);

	// Handle touch to change section
	const dragStart = useCallback(debounce(e => {
		if (e.touches) {
			if (e.touches.length > 1) {
				return;
			} else {
				e = e.touches[0];
			}
		}
		setOrigin({ x: e.screenX, y: e.screenY });
	}, 500, true), [setOrigin]);

	const drag = useCallback(e => {
		if (origin) {
			let point = e.touches ? e.touches[0] : e;
			let translation = { x: point.screenX - origin.x, y: point.screenY - origin.y };
			if (e.touches && Math.abs(translation.x) > Math.abs(translation.y)) {
				e.preventDefault();
				e.stopPropagation();
			}
			setTranslate(point.screenX - origin.x);
		}
	}, [setTranslate, origin]);

	const dragEnd = useCallback(e => {
		if (transitionEnd) {

			let behindThreshold = Math.abs(translate / window.outerWidth) > 0.3;
			if (behindThreshold) {
				const currentSection = refs[current].current;
				let nextSection = null;

				if (translate < 0) {
					nextSection = currentSection.nextElementSibling;
				} else {
					nextSection = currentSection.previousElementSibling;
				}
				if (nextSection != null) {
					goto(nextSection.getAttribute('id'));
				}
			}
		}
		setOrigin(null);
		setTranslate(0);
	}, [current, goto, transitionEnd, translate, refs, setOrigin, setTranslate]);

	useLayoutEffect(() => {
		const container = appContainer.current;
		const prevent = e => e.preventDefault();
		container.addEventListener('dragstart', prevent);
		container.addEventListener('touchstart', dragStart);
		container.addEventListener('touchmove', drag);
		container.addEventListener('touchend', dragEnd);
		container.addEventListener('touchcancel', dragEnd);
		return () => {
			container.removeEventListener('dragstart', prevent);
			container.removeEventListener('touchstart', dragStart);
			container.removeEventListener('touchmove', drag);
			container.removeEventListener('touchend', dragEnd);
			container.removeEventListener('touchcancel', dragEnd);
		}
	}, [appContainer, drag, dragEnd, dragStart]);

	// Handle top menu click to change section
	const handleNavClick = useCallback(e => {
		e.preventDefault();
		const sectionId = e.target.getAttribute('href').replace('#', '');
		if (sectionId !== current && transitionEnd) {
			goto(sectionId);
		}
	}, [current, goto, transitionEnd]);

	return <div className="page">
		<Nav items={BODIES} onClick={handleNavClick} currentItem={current} />
		<main className="container" ref={appContainer}>
			{BODIES.map(b => <Body key={b.id} body={b} ref={refs[b.id]} />)}
		</main>
	</div>
}

export default App;
