import React, { forwardRef } from 'react';
import useFetch from '../hooks/useFetch.js';
import '../style/Bodies.scss';

const oneUa = 149597887;
const apiUrl = 'https://api.le-systeme-solaire.net/rest/bodies/';

const Body = forwardRef(function ({ body }, ref) {

	let [data, loading] = useFetch(apiUrl + body.id);

	return <section id={body.id} className="content" data-side={body.reversed ? 'right' : 'left'} ref={ref} style={{ '--body-color': body.color }}>
		{body.reversed ?
			<div className="photo">
				<img className="system-body-left" alt={body.label} srcSet={body.img + ' 1000w, ' + body.img2 + ' 2000w, ' + body.img3 + ' 3000w, ' + body.img4 + ' 4000w'} sizes={body.sizes} />
			</div> : null}
		<div className="text">
			<h1>{body.label}<small>{body.type}</small></h1>
			<ul>
				<li><i className="icon-diameter"></i>{new Intl.NumberFormat('fr-FR').format(body.diameter)} km</li>
				{body.distanceFromSun ? <li><i className="icon-distance"></i>{new Intl.NumberFormat('fr-FR', { maximumFractionDigits: 2 }).format(body.distanceFromSun / oneUa)} ua&nbsp;&nbsp;|&nbsp;&nbsp;{new Intl.NumberFormat('fr-FR').format(body.distanceFromSun)} km</li> : null}
			</ul>
			<div className="description">
				{body.paragraphs.map((p, index) => <p key={index} className="description-content">{p}</p>)}
				{loading ? <p>Chargement des satellites...</p> :
					data.moons !== null ? <>
						<h3 className="description-content">Autres caractéristiques :</h3>
						<h4 className="description-content">{data.moons.length > 1 ? 'Satellites' : 'Satellite'} ({data.moons.length}) :</h4>
						<p className="description-content">
							{data.moons.sort((m1, m2) => m1.moon.normalize("NFD").replace(/[\u0300-\u036f]/g, "") > m2.moon.normalize("NFD").replace(/[\u0300-\u036f]/g, "") ? 1 : -1).map((m, index) => (index !== 0 ? ', ' : '') + m.moon)}
						</p>
					</> : body.type !== 'Étoile' ? <><h3 className="description-content">Autres caractéristiques :</h3><p className="description-content">Aucun satellite naturel</p></> : null
				}
			</div>
		</div>
		{!body.reversed ?
			<div className="photo">
				<img className="system-body-right" alt={body.label} srcSet={body.img + ' 1000w, ' + body.img2 + ' 2000w, ' + body.img3 + ' 3000w, ' + body.img4 + ' 4000w'} sizes={body.sizes} />
			</div> : null}
	</section>
});

export default Body;
