/**
 * Retourne une fonction qui, tant qu'elle continue à être invoquée,
 * ne sera pas exécutée. La fonction ne sera exécutée que lorsque
 * l'on cessera de l'appeler pendant plus de N millisecondes.
 * Si le paramètre `immediate` vaut vrai, alors la fonction
 * sera exécutée au premier appel au lieu du dernier.
 * @param {function} func la fonction à `debouncer`
 * @param {number} wait le nombre de millisecondes (N) à attendre avant d'appeler func()
 * @param {boolean} immediate Appeler func() à la première invocation au lieu de la dernière (Faux par défaut)
 * @param {*} context le contexte dans lequel appeler func() (this par défaut)
 * @returns {*} résultat de la function
 */
export const debounce = function (func, wait, immediate, context) {
	var result;
	var timeout = null;
	return function () {
		var ctx = context || this, args = arguments;
		var later = function () {
			timeout = null;
			if (!immediate) result = func.apply(ctx, args);
		};
		var callNow = immediate && !timeout;
		// Tant que la fonction est appelée, on reset le timeout.
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) result = func.apply(ctx, args);
		return result;
	};
};

/**
 * Retourne une fonction qui, tant qu'elle est appelée,
 * n'est exécutée au plus qu'une fois toutes les N millisecondes.
 * @param {function} func la fonction à contrôler
 * @param {number} wait le nombre de millisecondes (période N) à attendre avant de pouvoir exécuter à nouveau la function func()
 * @param {boolean} leading Appeler également func() à la première invocation (Faux par défaut)
 * @param {boolean} trailing Appeler également func() à la dernière invocation (Faux par défaut)
 * @param {*} context e contexte dans lequel appeler func() (this par défaut)
 * @returns {*} résultat de la function
 */
export const throttle = function (func, wait, leading, trailing, context) {
	var ctx, args, result;
	var timeout = null;
	var previous = 0;
	var later = function () {
		previous = new Date();
		timeout = null;
		result = func.apply(ctx, args);
	};
	return function () {
		var now = new Date();
		if (!previous && !leading) previous = now;
		var remaining = wait - (now - previous);
		ctx = context || this;
		args = arguments;
		// Si la période d'attente est écoulée
		if (remaining <= 0) {
			// Réinitialiser les compteurs
			clearTimeout(timeout);
			timeout = null;
			// Enregistrer le moment du dernier appel
			previous = now;
			// Appeler la fonction
			result = func.apply(ctx, args);
		} else if (!timeout && trailing) {
			// Sinon on s’endort pendant le temps restant
			timeout = setTimeout(later, remaining);
		}
		return result;
	};
};

/**
 * Native scrollTo with callback
 * @param offset - offset to scroll to
 * @param callback - callback function
 */
export const scrollTo = function (offset) {
	// const fixedOffset = offset.toFixed();

	// const onScroll = function () {
	// 	if (window.pageYOffset.toFixed() === fixedOffset) {
	// 		window.removeEventListener('scroll', onScroll)
	// 		callback();
	// 	}
	// }

	// window.addEventListener('scroll', onScroll)
	// onScroll();
	// window.scrollTo({
	// 	top: offset,
	// 	behavior: 'smooth'
	// });

	return new Promise((resolve, reject) => {

		const fixedOffset = offset.toFixed();

		const onScroll = function () {
			if (window.pageYOffset.toFixed() === fixedOffset) {
				window.removeEventListener('scroll', onScroll)
				resolve();
			}
		}

		window.addEventListener('scroll', onScroll)
		onScroll();

		window.scrollTo({
			top: offset,
			behavior: 'smooth'
		});
	});
};
