import React, { createRef, useMemo, useRef, useEffect } from 'react';
import gsap from 'gsap';
import '../style/Nav.scss';

const Nav = function ({ items, onClick, currentItem }) {

	let navRef = useRef(null);
	let refs = useMemo(() => ({
		'soleil': createRef(null),
		'mercure': createRef(null),
		'venus': createRef(null),
		'terre': createRef(null),
		'mars': createRef(null),
		'jupiter': createRef(null),
		'saturne': createRef(null),
		'uranus': createRef(null),
		'neptune': createRef(null),
		'pluton': createRef(null)
	}), []);
	let svgRef = useRef(null);

	useEffect(() => {
		gsap.to(svgRef.current, { duration: 1.5, y: 10, repeat: -1, yoyo: true, ease: "power1.inOut" });
	}, [svgRef]);

	useEffect(() => {
		let current = refs[currentItem].current;
		let left = current.offsetLeft + (current.offsetWidth / 2) - (svgRef.current.clientWidth / 2);
		svgRef.current.style.left = 100 * left / navRef.current.offsetWidth + '%';
		svgRef.current.style.color = items.filter(i => i.id === currentItem)[0].color || 'white';
	}, [currentItem, refs, svgRef, items]);

	return <nav ref={navRef}>
		<ul>
			{items.map(i => <li key={i.id} ref={refs[i.id]}><a href={'#' + i.id} className={'scroll' + (i.id === currentItem ? ' active' : '')} onClick={onClick} title={'Se rendre sur ' + i.label}>{i.shortLabel}</a></li>)}
		</ul>
		<svg ref={svgRef} fill="currentColor" width="20px" height="20px" x="0px" y="0px" viewBox="0 0 1 1">
			<polygon points="0.5,0 0.11,1 0.5,0.79 0.89,1 " />
		</svg>
	</nav>
};

export default Nav;
